export default {
	data() {
		return {
			couponId:'',
			couponInfo:{
				add_time:"暂无数据",
				end_time:"暂无数据",
				coupon_price:"0"
			},
		}
	},
	onLoad(e) {
		this.couponId=e.couponId;
		this.getCouponInfo();
	},
	methods: {
		getCouponInfo(){
			let that=this;
			that.baseGet(
				that.U({ c: 'coupons_api', a: 'get_coupon_rope',q: {
					couponId: that.couponId
				}}),
				function(res) {
					that.couponInfo=res.data;
					console.log(that.couponInfo);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		detial(e) {
			uni.navigateTo({
				url: "/pages/list/goode_details/goode_details"
			})
		},
		shop(e) {
			uni.navigateTo({
				url: "/pages/shop/shop_commodity/shop_commodity"
			})
		},
	}
}
